.navbar-virtual-width {
  width: 100vw;
}

.nav-brand {
 font: 400 1rem Roboto;
}

.user-form-input {
  min-width: 15rem;
}

.content-wrapper {
  height: 100vh;
  min-width: 1200px;
  margin-top: 4.5rem;
  overflow-x: scroll;
}

.user-profile-image {
  height: 200px;
  width: 200px;
  margin: 1.5rem 1.5rem 0 1.5rem;
}

.user-profile-details {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  align-content: flex-start;
}

.toast-image {
  width: 20px;
  height: 20px;
}

.list-scroll {
  height: 80vh;
  overflow-y: scroll;

  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.list-scroll::-webkit-scrollbar {
  display: none;
}

@media (max-width: 767px) {
  .user-form-input {
    margin: 0.6rem 0;
  }
}

@media (max-width: 575px) {
  .user-form-button {
    margin-left: auto !important;
  }
}